/* animations */
/* gold fish rotations */
@-webkit-keyframes rotation0 {
  0% {}
  100% {-webkit-transform-origin: center;transform-origin: center;-webkit-transform: rotate(0deg);transform: rotate(0deg);}
}
@keyframes rotation0 {
  0% {}
  100% {-webkit-transform-origin: center;transform-origin: center;-webkit-transform: rotate(0deg);transform: rotate(0deg);}
}
@-webkit-keyframes rotation90 {
  0% {}
  100% {-webkit-transform-origin: center;transform-origin: center;-webkit-transform: rotate(45deg);transform: rotate(45deg);}
}
@keyframes rotation90 {
  0% {}
  100% {-webkit-transform-origin: center;transform-origin: center;-webkit-transform: rotate(45deg);transform: rotate(45deg);}
}
@-webkit-keyframes rotation180 {
  0% {}
  100% {-webkit-transform-origin: center;transform-origin: center;-webkit-transform: scaleX(-1);transform: scaleX(-1);}
}
@keyframes rotation180 {
  0% {}
  100% {-webkit-transform-origin: center;transform-origin: center;-webkit-transform: scaleX(-1);transform: scaleX(-1);}
}
@-webkit-keyframes rotation270 {
  0% {}
  100% {-webkit-transform-origin: center;transform-origin: center;-webkit-transform: rotate(-45deg);transform: rotate(-45deg);}
}
@keyframes rotation270 {
  0% {}
  100% {-webkit-transform-origin: center;transform-origin: center;-webkit-transform: rotate(-45deg);transform: rotate(-45deg);}
}
/* gold fish */
@-webkit-keyframes swing1 {
  0%  {-webkit-transform: translate(0px,-9px);transform: translate(0px,-9px);}
  100% {-webkit-transform: translate(0px,9px);transform: translate(0px,9px);}
}
